.area1 , .area2 , .area3 , .area4 , .area5 , .area6 , .area7, .area8{
  background-color:#eff7ef;
}

.areaflag , .areawhat{
  background-image:-webkit-radial-gradient( #f83600 0%, #f9d423 100%);
  background-image:radial-gradient( #f83600 0%, #f9d423 100%);
}

.areawrong , .areabang{
  background-image:-webkit-radial-gradient(#e81900 100%, #c7eafd 0%);
  background-image:radial-gradient(#e81900 100%, #c7eafd 0%);
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

